



/* ! 1.Стили для устройств с шириной экрана до 768px */
/* ! 1.Стили для устройств с шириной экрана от 320px до 768px */
/* @media (max-width: 767px) { */
/* @media screen and (min-width: 320px) { */
@media screen and (max-width: 767px) {
    .headerShops {
            font-size: 20px;
            text-align: center;
            margin: 0;
            margin-top: 10px;
        }
    
        .list1 {
            align-self: flex-start;
            /* flex-grow: 1; */
            /* height: 50%; */
            display: flex;
            flex-wrap: wrap;
            /* flex-direction: column; */
            gap: 20px;
            /* gap: 1%; */

            /* justify-content: flex-start; */
            /* align-items: flex-start; */
            margin: 0;
            padding: 10px;
            list-style: none;
        }

        .list {
            flex-grow: 1;
            display: grid;
                /* ! Задаем три столбца равной ширины */
                grid-template-columns: 1fr 1fr 1fr;
                /* ! Отступ между элементами */
                /* grid-gap: 20px; */
                gap: 2%;
                
            margin: 0;
            padding: 10px;
            list-style: none;
        }
    
        .listItem {
            margin-left: auto;
            margin-right: auto;
    
        }
    
        .selectShopButton {
            min-width: 120px;
            text-decoration: none;
            padding: 8px 10px;
            font-weight: 700;
            border: solid 1px;
            border-radius: 4px;
            color: #2a363b;
            box-shadow: 12px 12px 18px rgba(59, 43, 14, 0.23);
        }
    
        .selectShopButton:hover,
        .selectShopButton:focus {
            background-color: #c4c4c4;
        }
}



/* ! 2.Стили для устройств с шириной экрана от 768px и выше */
/* @media (min-width: 768px) { */
@media screen and (min-width: 768px) {
    .headerShops {
        font-size: 20px;
        text-align: center;
        margin: 0;
        margin-top: 10px;
    }

    .list {
        /* height: 50%; */
        display: flex;
        flex-direction: column;
        gap: 40px;
        justify-content: flex-start;
        margin: 0;
        padding: 10px;
        list-style: none;
    }

    .listItem {
        margin-left: auto;
        margin-right: auto;

    }

    .selectShopButton {
        min-width: 120px;
        text-decoration: none;
        padding: 8px 10px;
        font-weight: 700;
        border: solid 1px;
        border-radius: 4px;
        color: #2a363b;
        box-shadow: 12px 12px 18px rgba(59, 43, 14, 0.23);
    }

    .selectShopButton:hover,
    .selectShopButton:focus {
        background-color: #c4c4c4;
    }
}

















