        font-weight: 600;

        border-radius: 8px;
        background-color: #faf8e2;
        box-shadow: 12px 12px 18px rgba(59, 43, 14, 0.23);

        /* outline: 1px solid tomato; */
        }
    
    
    .shops {
        overflow-y: auto;
        /* overflow-x: hidden; */
        width: 95%;
        height: 25vh;
        /* min-height: 35%; */
        min-height: 170px;
        /* flex-basis: 90%; */
        /* min-width: 15%; */
        /* height: 20%; */
        /* flex-grow: 1; */

        /* width: 90vw; */
        /* height: 100vh; */


        display: flex;
        flex-direction: column;
        /* justify-content: center; */
        /* align-items: center; */
        gap: 20px;

        border: 2px solid rgb(120, 196, 130);
        border-radius: 8px;
        box-shadow: 12px 12px 18px rgba(59, 43, 14, 0.23);

        /* outline: 1px solid tomato; */
    }
    
    .pizzas {
        overflow-y: auto;
        width: 95%;
        /* height: 20%; */
        /* flex-basis: 483px; */
        /* height: 100%; */
        flex-grow: 1;
        align-items: center;

        border: 2px solid rgb(120, 196, 130);
        border-radius: 8px;
        box-shadow: 12px 12px 18px rgba(59, 43, 14, 0.23);

        /* outline: 1px solid tomato; */
    }

    .imagePizzaContainer {
        width: 100%;
        height: 100%;
        display: flex;

        align-items: center;
        justify-content: center;

        /* padding: 10px; */
        /* border: 2px solid rgb(120, 196, 130); */

            /* border-radius: 8px; */
            /* box-shadow: 12px 12px 18px rgba(59, 43, 14, 0.23); */

        /* outline: 5px solid tomato; */
    }

    .imageBackgroundPizza {
        /* width: 95%; */
        /* height: 95%; */
        width: 45vh;

        border-radius: 50%;
            box-shadow: 9px 9px 12px rgba(44, 35, 18, 0.23);
        
            animation-name: spin;
            animation-duration: 20s;
            animation-timing-function: linear;
            animation-iteration-count: infinite;
            animation-direction: alternate;
        /* outline: 5px solid tomato; */
    }
    @keyframes spin {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
}




